import React, { createContext, ReactNode } from 'react';
import { fetchAllData } from '../data/fetch_data';

interface ProductProviderProps {
    children: ReactNode;
}

interface ProductContextProps {
    products: IProduct[];
    setProducts: React.Dispatch<React.SetStateAction<IProduct[]>>;
    search: (query: string) => void;
    resetSearch: () => void;
    searchQuery: IProduct[];
    setSearchQuery: React.Dispatch<React.SetStateAction<IProduct[]>>;
    inputValue: string;
    setInputValue: React.Dispatch<React.SetStateAction<string>>;
}

export const ProductContext = createContext<ProductContextProps>({
    products: [],
    setProducts: () => {},
    search: () => {},
    resetSearch: () => {},
    searchQuery: [],
    setSearchQuery: () => {},
    inputValue: '',
    setInputValue: () => {},
});

export const ProductProvider: React.FC<ProductProviderProps> = ({ children }) => {
    const [products, setProducts] = React.useState<IProduct[]>([]);
    const [searchQuery, setSearchQuery] = React.useState<IProduct[]>([]);
    const [inputValue, setInputValue] = React.useState('');

    React.useEffect(() => {
        const fetchData = async () => {
            try {
                const data = await fetchAllData();
                const sortedData = data.sort((a, b) => {
                    if (a.labelName && b.labelName) {
                        return a.labelName.localeCompare(b.labelName);
                    }
                    return 0;
                });
                setProducts(sortedData);
                setSearchQuery(sortedData);
            } catch (error) {
                console.error('Error fetching data')
            }
        };
    
        fetchData();
    }, []);

    const search = (query: string) => {
        setInputValue(query);
        const filteredProducts = searchQuery.filter((product) => {
            return product.labelName?.toLowerCase().includes(query.toLowerCase());
        });
        setProducts(filteredProducts);
    };

    const resetSearch = () => {
        setProducts(searchQuery);
        setInputValue('');
    };

    return (
        <ProductContext.Provider value={{ 
            products,
            setProducts,
            search,
            resetSearch,
            searchQuery,
            setSearchQuery,
            inputValue,
            setInputValue
        }}>
            {children}
        </ProductContext.Provider>
    );
};